// =============================================================================
// X402 METRICS API
// =============================================================================
// Public API endpoint for x402 health and metrics
// Feeds the public metrics dashboard

import { NextRequest, NextResponse } from 'next/server';
import { getX402CallLogs } from '@/integrations/x402/x402-call-log';
import { listUsageLogs } from '@/db/metered-endpoints';
import { getEndpoint } from '@/db/metered-endpoints';
import { getCDPFacilitator } from '@/integrations/x402/cdp-facilitator';

/**
 * GET /api/x402/metrics
 * 
 * Returns x402 system health and metrics
 * 
 * Query params:
 * - endpointId: Filter by specific endpoint (optional)
 * - hours: Time window in hours (default: 24)
 */
export async function GET(request: NextRequest) {
  try {
    const searchParams = request.nextUrl.searchParams;
    const endpointId = searchParams.get('endpointId') || undefined;
    const hours = parseInt(searchParams.get('hours') || '24', 10);
    
    // Calculate time window
    const now = new Date();
    const startDate = new Date(now.getTime() - hours * 60 * 60 * 1000).toISOString();
    
    // Get usage logs
    const usageLogs = await listUsageLogs({ endpointId });
    const recentLogs = usageLogs.filter(log => log.timestamp >= startDate);
    
    // Get x402 call logs for facilitator breakdown
    const x402Logs = endpointId 
      ? await getX402CallLogs(endpointId, { startDate })
      : await Promise.all(
          (await import('@/db/metered-endpoints')).listEndpoints()
            .then(endpoints => 
              Promise.all(
                endpoints.map(ep => getX402CallLogs(ep.id, { startDate, limit: 1000 }))
              )
            )
            .then(logArrays => logArrays.flat())
        );
    
    // Calculate metrics
    const totalRequests = recentLogs.length;
    const successfulRequests = recentLogs.filter(log => log.statusCode === 200).length;
    const failedRequests = recentLogs.filter(log => log.statusCode >= 400).length;
    const paymentRequiredRequests = recentLogs.filter(log => log.statusCode === 402).length;
    
    // Facilitator breakdown
    const facilitatorCounts: Record<string, number> = {};
    x402Logs.forEach(log => {
      facilitatorCounts[log.facilitator] = (facilitatorCounts[log.facilitator] || 0) + 1;
    });
    
    // Error breakdown
    const errorCounts: Record<string, number> = {};
    recentLogs.forEach(log => {
      if (log.failureCode) {
        errorCounts[log.failureCode] = (errorCounts[log.failureCode] || 0) + 1;
      }
    });
    
    // Average response time
    const avgResponseTime = recentLogs.length > 0
      ? recentLogs.reduce((sum, log) => sum + log.responseTime, 0) / recentLogs.length
      : 0;
    
    // P95 response time
    const sortedResponseTimes = recentLogs
      .map(log => log.responseTime)
      .sort((a, b) => a - b);
    const p95Index = Math.floor(sortedResponseTimes.length * 0.95);
    const p95ResponseTime = sortedResponseTimes[p95Index] || 0;
    
    // Check CDP health
    let cdpHealth = { healthy: false, error: 'Not checked' };
    try {
      const cdp = getCDPFacilitator();
      cdpHealth = await cdp.healthCheck();
    } catch (error) {
      cdpHealth = { 
        healthy: false, 
        error: error instanceof Error ? error.message : 'Health check failed' 
      };
    }
    
    // Get endpoint info if specified
    let endpointInfo = null;
    if (endpointId) {
      const endpoint = await getEndpoint(endpointId);
      if (endpoint) {
        endpointInfo = {
          id: endpoint.id,
          name: endpoint.name,
          status: endpoint.status,
          price: endpoint.price,
          network: endpoint.network,
        };
      }
    }
    
    return NextResponse.json({
      timestamp: now.toISOString(),
      timeWindow: {
        hours,
        startDate,
        endDate: now.toISOString(),
      },
      health: {
        cdp: cdpHealth,
        verifyMode: process.env.X402_VERIFY_MODE || 'off',
      },
      metrics: {
        totalRequests,
        successfulRequests,
        failedRequests,
        paymentRequiredRequests,
        successRate: totalRequests > 0 ? (successfulRequests / totalRequests) * 100 : 0,
        avgResponseTime: Math.round(avgResponseTime),
        p95ResponseTime: Math.round(p95ResponseTime),
      },
      breakdown: {
        byFacilitator: facilitatorCounts,
        byError: errorCounts,
      },
      endpoint: endpointInfo,
    });
  } catch (error) {
    return NextResponse.json(
      {
        error: 'Failed to fetch metrics',
        message: error instanceof Error ? error.message : 'Unknown error',
        timestamp: new Date().toISOString(),
      },
      { status: 500 }
    );
  }
}



